@use 'base/mixins' as m;
@use 'base/variables' as v;
@use 'sass:color';

.hero {
    position: relative;
    max-width: 100vw;
    min-height: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &__background {
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        z-index: -1;
    }

    &__background-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 80%;
    }

    &__overlay {
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(v.$black, .5);
    }

    &__content {
        width: 80%;
        height: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-right: 2.14rem;

        @include m.phone {
            margin-right: auto;
        }
    }

    &__line {
        position: absolute;
        left: 20rem;
        top: 10rem;
        z-index: 3;
        width: 1rem;
        height: 15rem;
        background-color: v.$yellow-2;   

        @include m.phone {
            height: 12rem;
            width: .7rem;
            top: 9rem;
            left: 2rem;
        }
    }

    &__title {
        font-size: v.$h1-font-size;
        color: v.$white;
        text-transform: uppercase;
        line-height: 9rem;
        
        @include m.phone {
            font-size: v.$h1-font-size-mobile;
            line-height: 4.5rem;
        }
    }

    &__subtitle {
        font-size: v.$h3-font-size;
        color: v.$white;
        font-weight: 600;
        margin-top: 2.5rem;

        @include m.phone {
            font-size: v.$h2-font-size-mobile;
            line-height: 4.5rem;
        }
    }

    &__button {
        margin-top: 2.5rem;
        width: 25rem;
        
        @include m.boton(v.$yellow-2, v.$black);
    }
}